html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 0;
  background-color: #ffffff;
  color: #000000;
  font-family: "Marcellus", serif;
  line-height: 1.3;
  margin: 0;
}

.main {
  padding: 20px;
  max-width: 1040px;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (min-width: 1000px) {
  .content {
    flex: 3;
  }
}

@media only screen and (min-width: 1000px) {
  aside {
    flex: 1;
  }
}

.button {
  padding: 12 18;
  font-size: 16px;
  display: block;
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  transition: all 400ms ease-in-out;
}
.button--yellow {
  border: 2px solid #FCDC17;
  color: #ffffff;
}
.button--yellow:hover {
  background-color: #FCDC17;
  color: black;
}
.button--yellowBlack {
  border: 2px solid #FCDC17;
  color: #000000;
}
.button--yellowBlack:hover {
  background-color: #FCDC17;
  color: black;
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-family: "Marcellus", Sans-serif;
  font-size: 50px;
  font-weight: 600;
  line-height: 1.2em;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}

.site__logo {
  width: 100%;
  padding: 1rem;
  border-bottom: 1px solid #FCDC17;
}
.site__logo img {
  width: 125px;
  margin: auto;
  display: block;
}

.site__nav {
  width: 100%;
  max-width: 1240px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1rem;
}
.site__nav .navSocials {
  width: 20%;
}
.site__nav .navSocials ul {
  list-style: none;
}
.site__nav .navSocials ul li {
  display: inline-block;
}
.site__nav .navSocials ul li:not(:first-child) {
  margin: 0 10px;
}
.site__nav .navSocials .navSocials__icon {
  width: 20px;
}
.site__nav .navigation ul {
  list-style: none;
  padding: 0;
}
.site__nav .navigation ul li {
  display: inline-block;
  color: #ffffff;
  margin: 0 10px;
  transition: 200ms ease-in-out;
  cursor: pointer;
}
.site__nav .navigation ul li:hover {
  color: #FCDC17;
}
.site__nav .navButton {
  width: 20%;
  text-align: end;
}
.site__nav .navButton .button {
  margin-left: auto;
}

.hero__background {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  background-image: url(../app/images/content/backgrounds/homepage_hero.jpg);
  margin: 0;
  padding: 0;
  height: 1000px;
  background-position: center;
  background-size: cover;
}
.hero__background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(180deg, #111 23%, hsla(0deg, 0%, 7%, 0) 52%);
  width: 100%;
  height: 100%;
  opacity: 0.65;
}
.hero__background::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background: url(./../app/images/content/backgrounds/hero_paperbottom.png);
  background-repeat: 0;
  background-position: bottom;
}

.hero {
  width: 100%;
  height: 700px;
  position: relative;
}
.hero .hero__inner {
  width: 100%;
  max-width: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #ffffff;
}
.hero .hero__inner small {
  color: #FCDC17;
}
.hero .hero__inner h1 {
  font-size: 52px;
  font-weight: 700;
}
.hero .hero__inner .button {
  margin: auto;
}

.row {
  display: flex;
}

.row--2c {
  gap: 30px;
}
.row--2c .col {
  width: calc(50% - 15px);
}

.row-3c {
  gap: 20px;
  flex-wrap: wrap;
}
.row-3c .col {
  width: calc(33% - 10px);
}

.mt-5 {
  margin-top: 5rem;
}

.section--about {
  padding: 150px 0;
}
.section--about h2 {
  font-size: 50px;
}
.section--about .image {
  width: 100%;
}
.section--about .aboutBox {
  width: 1070px;
  height: 350px;
  background: url(../app/images/content/backgrounds/aboutbox.jpg);
  margin: auto;
  margin-top: 100px;
}
.section--about .aboutBox .overlay {
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.85;
}
.section--about .aboutBox .aboutBox__item {
  display: flex;
  align-items: flex-start;
}
.section--about .aboutBox .aboutBox__item .icon {
  display: inline-block;
  font-size: 40px;
  line-height: 70px;
  text-align: center;
  height: 70px;
  width: 70px;
  background-color: #FCDC17;
  border-radius: 50%;
  color: #000000;
}
.section--about .aboutBox .aboutBox__item .icon__text {
  display: inline-block;
  width: 50%;
  margin-left: 50px;
}

.menuNav {
  min-height: 1000px;
  background: url(../app/images/content/backgrounds/menunav.jpg);
  position: relative;
  box-shadow: inset 0 99999px rgba(0, 0, 0, 0.7);
}
.menuNav .inner {
  max-width: 1140px;
  margin: auto;
  padding: 80px 0;
  z-index: 10;
}
.menuNav .inner .subtitle {
  color: #FCDC17;
  text-align: center;
  display: block;
}
.menuNav .inner h2 {
  color: #ffffff;
  text-align: center;
  font-size: 60px;
}
.menuNav .manuNav__boxes {
  display: flex;
  max-width: 1140px;
  margin: auto;
  justify-content: space-between;
  color: #ffffff;
  gap: 20px;
}
.menuNav .manuNav__boxes .menuNav__item {
  width: calc(25% - 10px);
  height: 500px;
  border: 5px solid #FCDC17;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 20px;
  box-shadow: inset 0 99999px rgba(0, 0, 0, 0.7);
  position: relative;
  cursor: pointer;
}
.menuNav .manuNav__boxes .menuNav__item--salat {
  background: url(../../app/images/content/menu/menunav/salat.jpg);
  background-position: center;
  background-size: cover;
}
.menuNav .manuNav__boxes .menuNav__item--main {
  background: url(../../app/images/content/menu/menunav/main.jpg);
  background-position: center;
  background-size: cover;
  margin-top: -30px;
}
.menuNav .manuNav__boxes .menuNav__item--dezerts {
  background: url(../../app/images/content/menu/menunav/dezert.jpg);
  background-position: center;
  background-size: cover;
}
.menuNav .manuNav__boxes .menuNav__item--drinks {
  background: url(../../app/images/content/menu/menunav/drinks.jpg);
  background-position: center;
  background-size: cover;
  margin-top: -30px;
}
.menuNav .manuNav__boxes .menuNav__item h3 {
  position: relative;
}
.menuNav .manuNav__boxes .menuNav__item h3::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 0%;
  height: 2px;
  background-color: #FCDC17;
  transition: all 400ms ease-in-out;
}
.menuNav .manuNav__boxes .menuNav__item .arrowLink {
  margin-top: 60px;
  position: absolute;
  bottom: 100px;
  left: 50%;
  transform: translate(-50%);
  transition: all 400ms ease-in-out;
}
.menuNav .manuNav__boxes .menuNav__item .arrowLink::after {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  left: 50%;
  transform: translate(-50%) rotate(225deg);
  bottom: -40px;
  border: solid #FCDC17;
  border-width: 2px 0 0 2px;
}
.menuNav .manuNav__boxes .menuNav__item:hover .arrowLink {
  bottom: 75px;
}
.menuNav .manuNav__boxes .menuNav__item:hover h3::after {
  width: 100%;
}